import { RECEIVE_CURRENT_USER, RECEIVE_LOGIN_ERRORS } from '../actions/session_actions'

export default (state = [], action) => {
    Object.freeze(state)

    switch (action.type) {
        case RECEIVE_CURRENT_USER:
            return [];
        case RECEIVE_LOGIN_ERRORS:
            return Object.assign([], { errors: action.errors })
        default:
            return state;
    }
}